<script>
  import { PredictionResponse } from "../stores/stores";
  import Loader from "../components/Loader.svelte";
  import Divider from "../components/Divider.svelte";
  import Spacer from "../components/Spacer.svelte";
  import config from "../config";

  let formBinding;
  const insertExampleValues = (event) => {
    config.fields.forEach((field) => {
      try {
        formBinding[field.id].value = field.testingValue;
      } catch (err) {}
    });
  };

  let resultLoading = false;
  const onSubmitHandler = (event) => {
    // Delete previous result
    PredictionResponse.set(null);

    // Form validation
    let formIsValid = true;
    config.fields.forEach((field) => {
      if (field.maxValue && event.target[field.id].value > field.maxValue) {
        formIsValid = false;
        window.pushToast(
          "Gestational Age (days) must be within 0 - 6 (inclusive)"
        );
      }
      if (field.type == "number" && isNaN(Number(event.target[field.id].value))) {
        formIsValid = false;
        window.pushToast(`Invalid value: ${field.label}`);
      }
    });
    if (!formIsValid) return;

    let totalGaDays1;
    let totalGaDays2;
    try {
      totalGaDays1 =
        Number(event.target.gaw1.value) * 7 + Number(event.target.gad1.value);
      totalGaDays2 =
        Number(event.target.gaw2.value) * 7 + Number(event.target.gad2.value);
    } catch (err) {
      formIsValid = false;
      window.pushToast("Invalid value in Gestational Age fields");
    }
    if (!formIsValid) return;
    resultLoading = true;

    // Data transformation
    let data;
    try {
      data = {
        ...Object.fromEntries(
          config.scanOneFeatures.map((id) => [
            id,
            event.target[id].value / totalGaDays1,
          ])
        ),
        ...Object.fromEntries(
          config.scanTwoFeatures.map((id) => [
            id,
            event.target[id].value / totalGaDays2,
          ])
        ),
      };
      // Map missing value to Python nan
      Object.keys(data).forEach((key) => {
        data[key] = data[key] == 0 ? null : data[key];
      });
    } catch (error) {
      console.log(error);
      formIsValid = false;
      window.pushToast("Invalid value fields. Please only enter number.");
    }
    if (!formIsValid) return;
    console.log(`Request body: ${JSON.stringify(data)}`);

    // Start calling API for prediction
    fetch(`${process.env.SVELTE_API_URL}/predict`, {
      method: "POST",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(`Response body: ${JSON.stringify(data)}`);
        PredictionResponse.update((_) => data);
        resultLoading = false;
      });

    // Timeout
    setTimeout(() => {
      if (resultLoading) {
        resultLoading = false;
        window.pushToast("Timed-out. Please try again later.");
      }
    }, Number(process.env.SVELTE_NETWORK_TIMEOUT));
  };
</script>

<div class="sidecard">
  <form on:submit|preventDefault={onSubmitHandler} bind:this={formBinding}>
    {#each config.fields as field}
      {#if field.type === "divider"}
        <Divider />
      {:else if field.type === "subheading"}
        <h6>{field.text}</h6>
      {:else if field.type === "text"}
        <p class="subheading-text">{@html field.text}</p>
      {:else if field.values}
        <div class="sidecard__field">
          <label for={field.id}>{field.label}</label>

          <div class="sidecard__field__inner">
            <select id={field.id} value={field.defaultValue}>
              {#each field.values as value}
                <option>{value}</option>
              {/each}
            </select>
          </div>
        </div>
      {:else}
        <div class="sidecard__field">
          <label for={field.id}>{field.label}{field.required ? "*" : ""}</label>

          <div class="sidecard__field__inner">
            <input type="text" required={field.required} id={field.id} />
            <!-- value={process.env.production === "true" ? "" : field.testingValue} -->
            {#if field.unit}
              <p class="sidecard__field__inner__unit">{field.unit}</p>
            {/if}
          </div>
        </div>
      {/if}
    {/each}

    <div class="actions">
      {#if resultLoading}
        <Loader />
      {:else}
        <div class="flex">
          <button type="submit">Get Inference</button>
          <button type="button" on:click={insertExampleValues}
            >Insert example values</button
          >
        </div>
      {/if}
    </div>
  </form>

  <span class="footnote">
    Data is only used for inference and is not saved.
  </span>

  <Spacer multiplier="1.5" />
</div>

