<script>
	import { onMount } from 'svelte';
	import { fade, fly } from 'svelte/transition';
	import { backOut } from 'svelte/easing';

	let toasts = []; 
	let retainMs = 3500; 

	let toastId = 0;
	const pushToast = (msg = '') => {
		toasts = [...toasts, {
			_id : ++toastId,
			msg
		}]; 
		setTimeout(() => {
			unshiftToast();
		}, retainMs);
	};

	
	const unshiftToast = () => {
		toasts = toasts.filter((a, i) => i > 0); 
	};

	onMount(() => {
		window.pushToast = pushToast;
	});
</script>

<style lang="scss">
  @import "../base.scss";

	.toast-wrapper {
		position: fixed;
		right: $padding;
		top: 0;
		text-align: left;
		z-index: 1000;
	}
	.toast-item {
		border-radius: 8px;
		padding: 12px 10px;
		margin: $padding auto;
    width: calc(100vw - ($padding * 2));
		background: rgba(0, 0, 0, 0.7);
		color: #fff;

    @media screen and (min-width: 640px) {
      max-width: 360px;
    }
	}
</style>

<div class="toast-wrapper">
	{#each toasts as toast (toast._id)}
	<div class="toast-item" in:fly="{{delay: 0, duration: 300, x: 100, y: 0, opacity: 0.1, easing: backOut}}" out:fade={{duration:500, opacity: 0}}>
		{toast.msg}
	</div>
	{/each}
</div>
