<script>
  import { Router, Route } from "svelte-navigator";

  import Toast from "./components/Toast.svelte";
  import Result from "./views/Result.svelte";
  import Header from "./views/Header.svelte";
  import Footer from "./views/Footer.svelte";
  import Sidecard from "./views/Sidecard.svelte";
  import Feedback from "./views/Feedback.svelte";
  import Redirector from "./helper/Redirector.svelte";

  // PRE-HEAT FUNCTION
  const preheatFunction = () => {
    fetch(`${process.env.SVELTE_API_URL}/`, {
      method: "GET",
      headers: {},
    }).then(() => {
      startPreheatTimer();
    });
  };
  const startPreheatTimer = () => {
    setTimeout(() => {
      preheatFunction();
    }, process.env.SVELTE_NETWORK_PREHEAT);
  };
  startPreheatTimer();
</script>

<Toast />
<Router primary={false}>
  <main>
    <Header />
    <Route component={Redirector} />
    <Route path="feedback" component={Feedback} />
    <Route path="fyp">
      <div class="content">
        <Sidecard />

        <div class="info">
          <Result />
          <Footer />
        </div>
      </div>
    </Route>
  </main>
</Router>

<style lang="scss">
  @import "./base.scss";

  main {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    background-color: $primary-background;
    max-width: 100vw;
  }

  .info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  @media (min-width: 640px) {
    main {
      max-width: none;
      max-height: 100vh;
    }

    .info {
      overflow-y: auto;
      scrollbar-width: none; /* Firefox */
      -ms-overflow-style: none; /* IE and Edge */
    }

    .info::-webkit-scrollbar {
      display: none;
    }

    .content {
      flex: 1;
      height: 0;
